import React from 'react';
import { NavLink } from 'react-router-dom';


const Common = (props) => {
  return (<>
    <section id="header" className="" >

    <div className="container-fluid nav-bg">

<div className="row">
<div className="col-10 mx-auto">
    
    <div className="row">

    <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column">
      <h1> {props.topic} <strong className="brand-name">Mohan</strong>
      </h1>
      <h2 className="my-3">
        We are the team of talented developers making responsive websites
      </h2>

<div className="mt-3">
  <NavLink to={props.visit} className="btn-get-started">{props.btnText}</NavLink>
</div>

</div>

<div className="col-lg-6 order-1 order-lg-2 header-img">
<img src={props.imgSrc} className="img-fluid animated" alt="loading failed !" />
</div>

</div>

</div>
</div>
</div>

    </section>
     </>);
}

export default Common;